module.exports = function (event) {
  return new Promise(function (resolve, reject) {
    var reader = new FileReader();
    var files = event.target.files;
    var len = files.length;

    if (len > 1) {
      reject(new DOMException("Only one file can be uploaded at a time"));
    } else {
      reader.onerror = function () {
        reader.abort();
        reject(new DOMException("Problem parsing input file."));
      };

      var file = files[0];

      reader.onload = function (evt) {
        var uint = new Uint8Array(evt.target.result);
        var bytes = [];
        uint.map(function (byte) {
          bytes.push(byte.toString(16));
        });
        var hex = bytes.join('').toUpperCase();
        var base64 = reader.result.split(',')[1];
        file.base64 = base64;
        file.binaryFileType = getMimetype(hex);
        resolve(file);
      };

      reader.readAsDataURL(file);
    }
  });
};

var getMimetype = function getMimetype(signature) {
  switch (signature) {
    case '89504E47':
      return 'image/png';

    case '47494638':
      return 'image/gif';

    case '25504446':
      return 'application/pdf';

    case 'FFD8FFDB':
    case 'FFD8FFE0':
      return 'image/jpeg';

    case '504B0304':
      return 'application/zip';

    default:
      return 'Unknown filetype';
  }
};